.options-bar {
    margin-top: 7rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    @include telefono {
        flex-direction: column;
        justify-content: unset;

        .searchbar {
            input {
                border: none;
                padding: 2.5rem;
                box-shadow: 0px 0px 13px 0px rgba(0, 0, 0, 0.16);
                border-radius: 0.8rem;
                margin-bottom: 2rem;
                width: 100%;
            }
        }

        .filter-selector {
            select {

                border: none;
                padding: 2.2rem;
                box-shadow: 0px 0px 13px 0px rgba(0, 0, 0, 0.16);
                border-radius: 0.8rem;
                margin-top: 2rem;

            }
        }

    }

